<template>
  <v-layout row>
    <vue-particles
          color="#dedede"
          :particleOpacity="0.7"
          :particlesNumber="80"
          shapeType="circle"
          :particleSize="4"
          linesColor="#dedede"
          :linesWidth="1"
          :lineLinked="true"
          :lineOpacity="0.4"
          :linesDistance="150"
          :moveSpeed="3"
          :hoverEffect="true"
          hoverMode="grab"
          :clickEffect="true"
          class="particles-js"
        ></vue-particles>
    <v-flex xs4 offset-xs4 md4 offset-md4>
      <v-form v-model="valid" ref="form" lazy-validation @submit="submit" style="margin-top: 200px">
          <v-text-field
              label="Key"
              v-model="key"
              :rules="keyRules"
              :counter="5"
              required
          ></v-text-field>
          <v-btn outline  color="indigo" type="submit" :disabled="!valid">
            Go
            <v-icon right dark class="ml-1">keyboard_tab</v-icon>
          </v-btn>
        </v-form>
        <v-alert color="green lighten-2" icon="info" :value="true" v-if="error_message !== ''">
            {{error_message}}
          </v-alert>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'login',
  data: function () {
    return {
      valid: false,
      key: '',
      keyRules: [
        v => !!v || 'Key is required',
        v => (v && v.length === 5) || 'Key must be equal 5 characters'
      ]
    }
  },
  updated () {
    this.checkCurrentLogin()
  },
  methods: {
    submit (e) {
      e.preventDefault()
      this.$store.dispatch('user/login', this.key)
    },
    checkCurrentLogin(){
      if(!this.loggedIn  && this.$router.path !== '/'){
        this.$router.push('/')
      }else if(this.loggedIn){
        this.$router.push('/cv')
      }
    }
  },
  computed: {
    ...mapGetters({
      loggedIn: 'user/loggedIn',
      error_message: 'user/error_message'
    })
  }
}
</script>
<style scoped>
.particles-js {
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
</style>
