<template>
  <v-footer app style="height: 36px;">
    <v-layout row style="justify-content: space-between;">
      <v-flex xs3 style="align-self: center" text-xs-left>
        <v-btn flat @click="switchTheme()" style="margin-left: 0px">Switch Theme</v-btn>
      </v-flex>
      <v-flex xs6 style="align-self: center" text-xs-center>
        &copy;2019 — <strong>Tind</strong> - built by VueJS, FireStore, Vuetify
      </v-flex>
      <v-flex xs3 style="align-self: center; padding-right: 4px" text-xs-right>
        <div v-show="false">{{breakpointName}} {{breakpointWidth}} x {{breakpointHeight}}</div>
      </v-flex>
    </v-layout>
  </v-footer>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'my-footer',
  props: ['breakpointName', 'breakpointHeight', 'breakpointWidth'],
  methods: {
    ...mapActions('global', {
      switchTheme: 'changeTheme',
    }),
  },
}
</script>
