const state = {
  theme: 'dark',
}

const getters = {
  theme: state => { return state.theme },
}
const mutations = {
  changeTheme (state, payload) {
    if(state.theme === 'light') state.theme = 'dark'
    else state.theme = 'light'
  }
}
const actions = {
  changeTheme ({commit}, payload) {
    commit('changeTheme', payload)
  }
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
