import db from '../../firestore'
import { SET_LOGIN, SET_LOGOUT, SET_ERROR_MESSAGE } from './mutations'
import router from '../../router'

const state = {
  loggedIn: false,
  error_message: ''
}

const getters = {
  loggedIn: state => { return state.loggedIn },
  error_message: state => { return state.error_message }
}
const mutations = {
  [SET_LOGIN] (state, bool) {
    state.loggedIn = bool
  },
  [SET_ERROR_MESSAGE] (state, error) {
    state.error_message = error
  },
  [SET_LOGOUT] (state) {
    state.loggedIn = false
  }
}
const actions = {
  async login (context, key) {
    if (key.length !== 5) {
      return
    }
    context.commit(SET_ERROR_MESSAGE, '')
    var keys = []
    var collectRef = db.collection('keys')
    await collectRef.get().then((querySnapshot) => {
      querySnapshot.forEach((doc) => {
        keys = doc.data().keys
      })
    })
    if (keys.indexOf(key) !== -1) { // login success
      context.commit(SET_LOGIN, true)
      sessionStorage.setItem('loggedIn', true)
      router.push('/cv')
    } else { // login unsuccess
      sessionStorage.removeItem('loggedIn')
      context.commit(SET_LOGIN, false)
      context.commit(SET_ERROR_MESSAGE, 'Invalid key, try again.')
    }
  },
  async logout ({commit}) {
    commit(SET_LOGOUT)
    sessionStorage.removeItem('loggedIn')
  }
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
