<template>
  <v-app v-bind:dark="theme === 'dark' ? true : false">
    <v-content class="zindex">
      <v-slide-y-transition mode="out-in">
        <router-view></router-view>
      </v-slide-y-transition>
    </v-content>
    <my-footer :breakpointName="breakpointName" :breakpointWidth="breakpointWidth" :breakpointHeight="breakpointHeight" class="zindex"></my-footer>
  </v-app>
</template>

<script>
import { mapGetters } from 'vuex'
import MyFooter from './components/MyFooter'
export default {
  name: 'app',
  components: { MyFooter },
  computed: {
    breakpointName () {
      return this.$vuetify.breakpoint.name
    },
    breakpointWidth () {
      return this.$vuetify.breakpoint.width
    },
    breakpointHeight () {
      return this.$vuetify.breakpoint.height
    },
    ...mapGetters({
      theme: 'global/theme',
      loggedIn: 'user/loggedIn'
    })
  }
}
</script>

<style>
body {
  margin: 0;
}

main {
  text-align: center;
  margin-top: 0px;
}

.zindex {
  z-index: 999;
}


</style>
