<template>
  <v-container fluid class='home'>
    <div v-if="loggedIn">
      <v-layout row > <!-- Already login -->
        <!-- <v-btn
          absolute
          fab
          top
          right
          @click="print()"
        >
          <v-icon>mdi mdi-printer</v-icon>
        </v-btn> -->
        <v-flex class="personal" xs12 sm12 md10 offset-md1  lg8 offset-lg2 xl6 offset-xl3>
          <personal-info></personal-info>
        </v-flex>
      </v-layout>
      <v-layout>
        <v-flex class="career" xs12 sm12 md10 offset-md1  lg8 offset-lg2 xl6 offset-xl3>
          <career-info></career-info>
        </v-flex>
      </v-layout>
    </div>
    <div v-else>
      <v-layout row>  <!-- Didn't login yet -->
        <v-flex xs4 offset-xs4 md4 offset-md4>
          <v-btn large outline color="indigo" @click="backToLogin()">
            <v-icon left dark class="ml-1">keyboard_return</v-icon>
            Not login yet, back to login
          </v-btn>
        </v-flex>
      </v-layout>
    </div>
  </v-container>
</template>

<script>
import PersonalInfo from '@/components/HomeParts/PersonalInfo'
import CareerInfo from '@/components/HomeParts/CareerInfo'
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'home',
  components: { PersonalInfo, CareerInfo },
  data: function () {
    return {
    }
  },
  created () {
    this.checkCurrentLogin()
  },
  methods: {
    ...mapActions('user', {
      login: 'login',
      logout: 'logout'
    }),
    backToLogin: function () {
      this.$router.push('/')
    },
    print () {
      window.print();
      return false;
    },
    checkCurrentLogin(){
      console.log('this.loggedIn', this.loggedIn)
      if(!this.loggedIn  && this.$router.path !== '/'){
        this.$router.push('/')
      }else if(this.loggedIn){
        this.$router.push('/cv')
      }
    }
  },
  computed: {
    ...mapGetters({
      loggedIn: 'user/loggedIn',
      error_message: 'user/error_message'
    })
  }
}
</script>

<style>
.home {
  padding: 0px;
}
.personal {
  height: 210px;
  padding: 10px 0px 10px 10px;
}

.career {
}

</style>
