              <v-divider></v-divider>
            </div>
          </v-card-text>
          <v-card-title primary-title>
            <v-icon class='icon' title="day of birth">mdi mdi-cake-variant</v-icon>
            <div>{{dob}}</div>
          </v-card-title>
          <v-card-title primary-title>
            <v-icon class='icon' title="gender">mdi mdi-gender-male-female</v-icon>
            <div>Male</div>
          </v-card-title>
          <v-card-title primary-title>
            <v-icon class='icon' title="phone">mdi mdi-phone</v-icon>
            <div>{{phone}}</div>
          </v-card-title>
          <v-card-title primary-title>
